@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
}

.App {
  padding: 50px 100px;
}

.dark-app {
  background-color: hsl(230, 17%, 14%);
}

.dark-app .dash-container .card-dash-container,
.dark-app .overview-container .overview-container-data .overview-card {
  background-color: hsl(228, 28%, 20%);
}

.dark-app .dash-container .card-dash-container input {
  background-color: hsl(232, 19%, 15%);
  color: #fff;
}

.dark-app span,
.dark-app h1,
.dark-app h2 {
  color: #fff;
}

.dark-app span a {
  color: gray;
  font-weight: 700;
}

input {
  cursor: pointer;
  outline: none;
}

@media (max-width: 450px) {
  .dark-app,
  .App {
    padding: 30px;
  }
  .dark-app .dash-container {
    grid-template-columns: auto;
    grid-row-gap: 30px;
    row-gap: 30px;
  }
  .dark-app .overview-container .overview-container-data {
    grid-template-columns: auto;
    grid-row-gap: 30px;
    row-gap: 30px;
  }
  .dark-app .header-container {
    flex-direction: column;
    align-items: flex-start;
  }
  .dark-app .header-container .info-container h1 {
    font-size: 1em;
  }
  .dark-app .header-container .info-container span {
    font-size: 0.7em;
  }
  .dark-app .header-container .darkmode-container {
    margin-top: 10px;
    width: 100%;
  }
  .dark-app .header-container .darkmode-container label {
    width: 100%;
    justify-content: space-between;
  }
}

.header-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-container .darkmode-container label {
  color: hsl(230, 22%, 74%);
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-container .darkmode-container input {
  opacity: 0;
  width: 0;
  height: 0;
}

.header-container .darkmode-container span {
  position: relative;
  background-color: hsl(230, 22%, 74%);
  min-width: 50px;
  min-height: 26px;
  border-radius: 20px;
  cursor: pointer;
  margin-left: 10px;
}

.header-container .darkmode-container span::before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 25px;
  bottom: 3px;
  background-color: #fff;
  border-radius: 50%;
}

.header-container .darkmode-container input:checked + span {
  background-image: linear-gradient(
    to right,
    hsl(210, 78%, 56%),
    hsl(146, 68%, 55%)
  );
}

.header-container .darkmode-container input:checked + span::before {
  left: 5px;
}

@media (max-width: 450px) {
  .header-container {
    flex-direction: column;
    align-items: flex-start;
  }
  .header-container .info-container h1 {
    font-size: 1em;
  }
  .header-container .info-container span {
    font-size: 0.7em;
  }
  .header-container .darkmode-container {
    margin-top: 10px;
    width: 100%;
  }
  .header-container .darkmode-container label {
    width: 100%;
    justify-content: space-between;
  }
}

.dash-container {
  margin: 20px 0px;
  display: grid;
  grid-template-columns: 23% 23% 23% 23%;
  grid-column-gap: 30px;
}

.dash-container .card-dash-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: hsl(227, 47%, 96%);
  padding: 20px;
  border-radius: 5px;
}

.dash-container .card-dash-container .user-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.dash-container .card-dash-container .user-container span {
  font-size: 12px;
  margin-left: 10px;
}

.dash-container .card-dash-container .number-followers-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 15px;
}

.dash-container
  .card-dash-container
  .number-followers-container
  .number-followers {
  font-size: 40px;
  font-weight: 700;
}

.dash-container
  .card-dash-container
  .number-followers-container
  .followers-name {
  color: hsl(232, 10%, 57%);
  letter-spacing: 2px;
  font-size: 12px;
}

.dash-container .card-dash-container .statics-container {
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(26, 170, 26);
  font-size: 12px;
  font-weight: 700;
}

.dash-container .card-dash-container .statics-container img {
  margin-right: 5px;
}

.dash-container .card-dash-container input {
  margin-top: 15px;
  border: none;
  padding: 3px;
  text-align: center;
  border-radius: 5px;
}

@media (max-width: 450px) {
  .dash-container {
    grid-template-columns: auto;
    grid-row-gap: 30px;
    row-gap: 30px;
  }
}

.overview-container h2 {
  color: gray;
}

.overview-container .overview-container-data {
  margin-top: 10px;
  display: grid;
  grid-template-columns: 23% 23% 23% 23%;
  grid-template-rows: auto auto;
  grid-column-gap: 30px;
  -webkit-column-gap: 30px;
          column-gap: 30px;
  grid-row-gap: 30px;
  row-gap: 30px;
}

.overview-container .overview-container-data .overview-card {
  background-color: hsl(227, 47%, 96%);
  padding: 5px 15px;
  border-radius: 5px;
}

.overview-container .overview-container-data .overview-card div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 15px 0;
}

.overview-container
  .overview-container-data
  .overview-card
  .overview-title-card
  span {
  font-weight: 700;
  font-size: 0.9em;
}

.overview-container
  .overview-container-data
  .overview-card
  .overview-data-card
  .overview-card-number {
  font-size: 1.5em;
  font-weight: 700;
}

.overview-container
  .overview-container-data
  .overview-card
  .overview-data-card
  .overview-card-statistic {
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(19, 173, 19);
  font-size: 0.9em;
  font-weight: 700;
}

.overview-container
  .overview-container-data
  .overview-card
  .overview-data-card
  .overview-card-statistic
  img {
  margin-right: 3px;
}

@media (max-width: 450px) {
  .overview-container .overview-container-data {
    grid-template-columns: auto;
    grid-row-gap: 30px;
    row-gap: 30px;
  }
}

